
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'user',
props: ['loginname'],
components: {
Card,
Group,
Cell
},
data: function() {
return {
userinfo: null,
collect_topics:[]
}
},
beforeRouteEnter: function(to, from, next) {// 导航守卫
next(function(vm) {
vm.$http.get('/user/' + to.params.loginname).then(function(response) {// 发送ajax请求  捕获信息

vm.userinfo = response.data.data;
});


vm.$http.get('/topic_collect/' +to.params.loginname).then(function(response){
vm.collect_topics = response.data.data;
});
});
},
beforeRouteUpdate: function(to, from, next) {
var app = this;
this.$http.get('/user/' + to.params.loginname).then(function(response) {// 路由发生切换时发送ajax数据

app.userinfo = response.data.data;
});

app.$http.get('/topic_collect/' +to.params.loginname).then(function(response){
app.collect_topics = response.data.data;
});

}
}



